
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
}
#navbar {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background: #222222;
  border-bottom: 4px solid darkorange;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
}
#navbar h1 {
  display: inline-block;
  color: darkorange;
}
#navbar h2 {
  margin-top: 25px;
  margin-left: 70px;
  opacity: 0.8;
  color: darkcyan;
  z-index: 0;
  font-style: italic;
    cursor: pointer;
}
.instant-result-list {
  position: absolute;
  list-style: none;
  border: 1px solid black;
  height: 20vh;
  width: 75%;
  right: 5px;
  top: 67px;
  background: white;
  overflow-y: scroll;
  margin-top: 0;
  display: none;
  z-index: 2;
  border-radius: 2%;
  -webkit-box-shadow: 1px 1px 1px 1px darkcyan;
          box-shadow: 1px 1px 1px 1px darkcyan;
}
.instant-result-item {
  color: darkslategray;
}
.instant-result-item:hover {
  cursor: pointer;
  background-color: lightgrey;
}
.search-form {
 position: relative;
  width: 290px;
}
.search-form:before {
  content: ' ';
  position: absolute;
  left: 25px;
  top: 35px;
  width: 2px;
  height: 36px;
  -webkit-transform: scale(0);
          transform: scale(0);
  z-index: 3;
  background: rgb(71, 136, 135);
  -webkit-transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
}
input {
  border-radius: 36px;
  position: absolute;
  width: 50px;
  height: 50px;
  font-size: 20px;
  top: 10px;
  color: darkcyan;
  -webkit-transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  z-index: 2;
}
.active input {
  width: 100%;
  padding-left: 25px;
  border-radius: 36px;
}
button {
  background: rgba(255, 255, 255, 1);
  position: absolute;
  height: 100%;
  border-radius: 50%;
  top: 9px;
  width: 60px;
  height: 59px;
  -webkit-transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  transition: all 0.5s cubic-bezier(.87, -.41, .19, 1.44);
  z-index: 3;
  cursor: pointer;
}
button:after {
  content: ' ';
  position: absolute;
  width: 32px;
  height: 32px;
  left: 0;
  right: 0;
  top: 27px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin: 0 auto;
  background: darkcyan;
  border-radius: 50%;
}
button:before {
  content: ' ';
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  right: 0;
  top: 15px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: left top;
          transform-origin: left top;
  margin: 0 auto;
  background: white;
  border-radius: 50%;
  z-index: 3;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
}
.active button {
  -webkit-transform: translateX(260px);
          transform: translateX(260px);
}
.active button:before {
  -webkit-transform: scale(1);
          transform: scale(1);
}
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

